<template>
    <div class="error-wrapper">
        <slot name="icon">
            <div class="error-icon">
                <v-icon x-large class="grey--text text--lighten-2">error</v-icon>
            </div>
        </slot>
        <p class="error-message dark--text">{{message}}</p>
    </div>
</template>

<script>

export default {
    name: 'error',

    props: {
        message: {
            'type': String,
            'default': '未知错误'
        }
    },

    data() {
        return {
        };
    },

    computed: {
    },

    methods: {

    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


.error-wrapper
    background #fff
    width 100%
    text-align center

    .error-icon
        margin-top 40px

    .error-message
        margin-top 20px

</style>


