<template>
    <div class="b-loading" v-show="show">
        <div class="b-loading-logo-wrap">
            <div class="b-loading-logo"></div>
            <div class="b-loading-anim"></div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'b-loading',
    props: {
        show: Boolean
    },
    data() {
        return {};
    },
    computed: {
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

$logo = 'data:image/png;base64,';
$anim-pic = 'data:image/png;base64,'

@keyframes new-loading-anim {
    0% {
        left -80px
    }
    100% {
        left 170px
    }
}

.b-loading
    position absolute
    left 0
    top 0
    right 0
    bottom 0
    display inline-block
    background-color #fff
    text-align center

.b-loading-text
    color #888
    font 400 16px/30px normal

.b-loading-logo-wrap
    margin-top 210px
    height 60px
    width 150px
    display inline-block
    position relative

.b-loading-logo
    height 60px
    width 150px
    display inline-block
    background url($logo) 0 0 no-repeat
    background-size 100%
    opacity .7

.b-loading-anim
    position absolute
    width 60px
    height 60px
    background url($anim-pic) center center no-repeat
    top 0
    left -80px
    background-size cover
    animation new-loading-anim 1s linear infinite

</style>


